@import "./scss/base";
@import "./scss/fonts";
@import "./scss/vimeo";
@import "./scss/animate";
@import "./scss/info-hero";
@import "./scss/projects";
@import "./scss/cursor";
@import "./scss/background";
@import "./scss/responsive";

album* {
  margin: 0;
}

:root {
  --padding: 1.5rem;
  --color-black: #000;
  --color-white: #fff;
  --color-text: var(--color-black);
  --color-text-grey: var(--color-grey);
  --color-background: var(--color-white);
}

html {
  height: 100%;
  width: 100%;
  overflow: hidden;
  overscroll-behavior-x: none;
}

body {
  margin: 0 auto;
  padding: 0;
  overflow: hidden;
  height: 100%;
  width: 100%;
  overscroll-behavior-x: none;
  position: relative;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -moz-tap-highlight-color: rgba(0, 0, 0, 0);
}

li {
  list-style: none;
}

button {
  background: none;
  border: 0;
  outline: none;
  padding: 0;
  cursor: inherit;
}
